* {
  margin: 0;
  padding: 0;
  font-family: 'Oswald', sans-serif;
}



.GraWrapper {
  color: red;
  font-size: 10px; }

.GraWrapper button {
  margin: 0 20px;
  padding: 8px;
  font-size: 18px;
  border: red 2px solid;
  background-color: brown;
  color: white;
  font-weight: 300;
  text-transform: uppercase; }

.navigation{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    background-color: rgb(61, 61, 61);
    font-size: 16px;
  }
  
  
  .navul{
    list-style: none;
    }
  
  .navli{
    display: inline;
  }
  
  .navigation a{
    display:inline-flex;
    flex-grow: 1;
    text-align: center;
    color:white;
    padding: 0px 25px;
    text-decoration: none;
    text-transform: uppercase;
    margin: 0 auto;
    padding: 5px;
    width: 120px;  
    border-bottom: 10px solid rgb(61, 61, 61);
  }

  .navigation a:hover{
    color:yellow;
    -webkit-animation: App-logo-spin infinite 1s linear;
            animation: App-logo-spin infinite 1s linear;
    border-bottom: 10px solid rgb(141, 140, 140);
  }
  
  
  .active{
    
  }

  .navigation .info_selected{
    border-bottom-color: yellow;
  }
  
  .navigation .gra_selected{
    border-bottom-color:coral;
  }
  
  .navigation .ustawienia_selected{
    border-bottom-color: red;
  
  }
  
  .navigation .contact_selected {
    border-bottom-color: green;
  }

  .navigation .admin_selected {
    border-bottom-color: blue;
  }

  @-webkit-keyframes App-logo-spin {
    from {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
    to {
      -webkit-transform: translateY(5px);
              transform: translateY(5px);
    }
  }

  @keyframes App-logo-spin {
    from {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
    to {
      -webkit-transform: translateY(5px);
              transform: translateY(5px);
    }
  }
  
.HomePageWrapper {
  display: block;
  height: 100%;
  width: 100%; }

.HomePageWrapper article {
  display: block;
  width: 70%;
  background-color: #a0cfd1;
  padding: 20px;
  margin: 0 auto; }

.HomePageWrapper h1 {
  padding: 30px;
  font-size: 24px; }

.HomePageWrapper p {
  padding: 30px;
  font-size: 18px; }

.wrap {
  display: block;
  font-size: 20px;
  width: 300px; }

.headerClas {
  color: #f8e919;
  background-color: #3a3232; }

.custom-row-class {
  background-color: #f5eaea;
  text-align: center; }

.boo {
  display: block;
  margin: 10px 0px 0 10px;
  color: black;
  margin: 10px; }

.foo {
  width: 340px; }

.row-expand {
  font-size: 18px;
  color: blue; }

.row-expand:hover {
  font-size: 18px;
  color: green; }

.wybrane {
  display: block;
  margin-left: 10%; }

.wybrane li {
  list-style-image: url(data:image/png;base64,); }

.wybrane h4 {
  margin: 5px 0 5px 20%; }

.wybrane span {
  margin-left: 5%;
  margin-right: 5%; }

div.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 99%;
  margin: 0 auto;
  background-color: #f6f5f5; }

.header {
  display: flex;
  flex-basis: 2vh;
  background-color: #3d3d3d; }

.head {
  flex-grow: 1;
  background-color: #5af0f0;
  padding: 10px; }

.nav {
  flex-grow: 1; }

main {
  display: flex;
  flex-grow: 1;
  color: black;
  padding: 10px 0; }

section {
  flex-grow: 1;
  background-color: #fcf5ed; }

footer {
  background-color: slategray;
  color: yellow;
  padding: 20px; }

